export const PAD_COLORS = {
  white: '#ffffff',
  red: '#e52800',
  green: '#005923',
  blue: '#06afff',
  pink: '#ef47cc',
  yellow: '#fec41a',
  orange: '#ff6910',
  purple: '#760281',
}

export type PadColor = keyof typeof PAD_COLORS

type vec2 = [number, number]

export interface PadData {
  id: number
  pos: vec2
  isTerminal?: boolean
  color?: PadColor
}

export class Pad {
  public id: number
  public pos: vec2
  public isTerminal: boolean
  public color: PadColor | undefined
  public renderColor: PadColor

  constructor(id: number, pos: vec2, isTerminal: boolean = false, color?: PadColor) {
    if (isTerminal && !color) console.error('Terminal pad must have a color')
    if (!isTerminal && color !== undefined) console.error('Non-terminal pad cannot have a color')

    this.id = id
    this.pos = pos
    this.isTerminal = isTerminal
    this.color = isTerminal ? (color ?? 'red') : undefined
    this.renderColor = this.color ?? 'white'
  }

  toJSON(): PadData {
    if (this.isTerminal) {
      debug: if (!this.color) console.error('Terminal pad must have a color')
      return { pos: this.pos, id: this.id, isTerminal: this.isTerminal, color: this.color }
    } else {
      debug: if (this.color) console.error('Non-terminal pad cannot have a color')
      return { pos: this.pos, id: this.id }
    }
  }

  static fromJSON(data: PadData): Pad {
    return new Pad(data.id, data.pos, data.isTerminal, data.color)
  }
}
